.model__content {
    width: 70vw;
    margin-left: 5vw;
    margin-top: 10vh;
    display: inline-grid;
    grid-row: auto;
    justify-content: space-between;
    align-content: center;
    grid-template-columns: 1fr 1fr 1fr;
}


.model__image {
    width: 30vw;
    height: auto;
    border-radius: 20%;
}


@media screen and (max-width: 480px) {

    .model__content{
        margin-left: 10vw;
        width: 100vw;
        margin: auto;
        margin-top: -5vh;
        grid-template-columns: 1fr;
        margin: auto;
    }

    .model__image {
        margin-left: 20vw;
        margin-top: 2vh;
        width: 60vw;
    }

}